import { Component, OnDestroy, OnInit } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { AuthService } from '../core/auth.service';
import { UtilityService } from '../core/utility.service';
import { list } from 'src/app/signup/countryCode';
import {
  FormBuilder,
  FormGroup,
  FormArray,
  Validators,
  FormControl,
} from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.page.html',
  styleUrls: ['./edit-profile.page.scss'],
})
export class EditProfilePage implements OnInit, OnDestroy {
  profileData: any = {};
  previewImage: any = '';
  previewImage1: any = '';

  countryList = list;
  profileForm: FormGroup;
  sub$;
  constructor(
    private auth: AuthService,
    private utility: UtilityService,
    private sanitizer: DomSanitizer,
    private fb: FormBuilder,
    private activateRouter: ActivatedRoute
  ) {}

  ngOnInit() {
    this.sub$ = this.activateRouter.params.subscribe((res) => {
      this.buildForm();
      this.getProfile();
    });
  }

  buildForm() {
    this.profileForm = this.fb.group({
      firstname: ['', [Validators.required, Validators.pattern(/^[a-zA-Z]+$/)]],
      lastname: ['', [Validators.required, Validators.pattern(/^[a-zA-Z]+$/)]],

      fiscalNumber: [''],
      contact: [
        '',
        [
          Validators.pattern(/^\d+$/),
          Validators.minLength(10),
          Validators.maxLength(10),
        ],
      ],

      address: [''],
      country: ['', [Validators.required]],
      city: [''],
      zipCode: [''],
      email: [
        { value: '', disabled: true },
        [
          Validators.required,
          Validators.pattern(/[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/),
        ],
      ],
    });
  }

  get f() {
    return this.profileForm.controls;
  }

  getProfile() {
    this.utility.present();
    let userid = JSON.parse(localStorage.getItem('userObj'))._id;
    this.auth.getProfile({ id: userid }).subscribe((res) => {
      this.profileData = res;
      console.log(this.profileData);
      this.profileForm.patchValue({
        firstname: this.profileData.firstname,
        lastname: this.profileData.lastname,
        fiscalNumber: this.profileData.fiscalNumber,
        city: this.profileData.city,
        country: this.profileData.country,
        contact: this.profileData.contact || '',
        address: this.profileData.address,
        zipCode: this.profileData.zipCode,
        email: this.profileData.email,
        image: this.profileData.image,
        dogFooterImage: this.profileData.dogFooterImage,
      });

      this.previewImage = this.profileData.imageUrl;
      this.previewImage1 = this.profileData.dogImageUrl;

      console.log(this.previewImage);
      this.utility.dismiss();
    });
  }
  save() {
    if (this.profileForm.status.toLowerCase() == 'invalid') {
      return;
    }
    this.utility.present();
    let value = this.profileForm.value;
    if (this.previewImage != '') {
      value.image = this.profileData.image;
    } else {
      value.image = '';
    }
    if (this.previewImage1 != '') {
      value.dogFooterImage = this.profileData.dogFooterImage;
    } else {
      value.dogFooterImage = '';
    }
    value.email = this.profileData.email;
    this.auth.updateProfile(value).subscribe(
      (res) => {
        console.log(res, 'updated');
        this.utility.dismiss();
        //this.utility.navigate('tabs/profile');
        this.utility.navigate('tabs/profile-details');
      },
      (err) => {
        this.utility.dismiss();
      }
    );
  }
  handleFile(e) {
    this.profileData.image = e.target.files[0];
    this.previewImage = this.sanitizer.bypassSecurityTrustUrl(
      URL.createObjectURL(this.profileData.image)
    );
    console.log(this.previewImage);
  }
  removeImage() {
    this.previewImage = '';
    this.profileData.image = '';
  }
  handleFile1(e) {
    this.profileData.dogFooterImage = e.target.files[0];
    this.previewImage1 = this.sanitizer.bypassSecurityTrustUrl(
      URL.createObjectURL(this.profileData.dogFooterImage)
    );
    console.log(this.previewImage1);
  }
  removeImage1() {
    this.previewImage1 = '';
    this.profileData.dogFooterImage = '';
  }
  cancel() {
    this.utility.goBack();
  }
  goBack() {
    this.utility.goBack();
  }
  ngOnDestroy() {
    this.sub$.unsubscribe();
  }
}
